import './index.scss';
import { useTranslation } from 'react-i18next';
import BaseSectionTitle from '../../BaseSectionTitle';
import bigImg from '../../../assets/images/form.png';

function ContactForm() {
  const { t } = useTranslation();

  return (
    <div className="contact__form">
      <div className="container">
        <div className="contact__form__inner d-flex">
          <div className="contact__form__left">
            <BaseSectionTitle titleSmall="contactUs.contact" titleBig="contactUs.form" />
            <p className="text-md">{ t('contactUs.team') }</p>
            <form>
              <div className="input-line d-flex text-md">
                <input className="pn-regular" name="name" type="text" placeholder={ t('contactUs.name') } />
                <input className="pn-regular" name="phone" type="text" placeholder={ t('contactUs.phone') } />
              </div>
              <div className="input-line d-flex text-md">
                <input className="pn-regular" name="email" type="text" placeholder={ t('contactUs.email') } />
                <input className="pn-regular" name="city" type="text" placeholder={ t('contactUs.city') } />
              </div>
              <textarea className="pn-regular" name="message" placeholder={ t('contactUs.message') }></textarea>
              <button className="pn-semibold text-sm" type="submit">{ t('contactUs.book') }</button>
            </form>
          </div>
          <div className="contact__form__right">
            <img src={bigImg} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default ContactForm;