import './index.scss'
import { Fragment } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

function BaseSectionTop({ title, links }) {
  const { t } = useTranslation();

  return (
    <div className="base__section__top">
      <div className="container">
        <h1 className="base__section__top-title pn-bold">
          { t(title) }
        </h1>
        <div className="base__section__top-links text-sm">
          <Link to="/pga">{ t('header.main') }</Link>
          {
            links.map((link, index) => {
              return (
                <Fragment key={index}>
                  {' / '} 
                  <Link to={link.to}>{ t(link.title) }</Link>
                </Fragment>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default BaseSectionTop;